var render = function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"page-system"},[_c('BgTop'),_vm._ssrNode(" <div class=\"heading-en under-en-title\" data-v-7dbd852c>\n        System Development<br data-v-7dbd852c>Infrastructure Construction\n        <div class=\"heading__img\" data-v-7dbd852c></div></div> <div class=\"system__desc\" data-v-7dbd852c><h1 class=\"large-ja\" data-v-7dbd852c><span class=\"ja\" data-v-7dbd852c>サービス内容</span></h1> <div class=\"desc__txt\" data-v-7dbd852c><div class=\"txt-bg\" data-v-7dbd852c></div> <h3 data-v-7dbd852c>ＢＦＴでは、主にインフラ基盤構築とシステム開発でサービスを提供しています。</h3> <div class=\"desc__txt__txt\" data-v-7dbd852c>\n                ＢＦＴはインフラ基盤技術に集中することで、それを自社の強みとして成長してきました。その成長過程において「テクノロジーをオープンにすること」の大切さを学びました。我々が得た知的な資産をオープンにし、それを多くのお客様へお届けすること、そこからさらにフルスタックに技術を追求していくこと、これが我々のテクノロジーに対する基本スタンスです。\n            </div></div></div> <section class=\"system__sysdev\" data-v-7dbd852c><div class=\"side-red\" data-v-7dbd852c></div> <h2 class=\"system__sysdev__title\" data-v-7dbd852c><span class=\"ja\" data-v-7dbd852c>システム開発</span></h2> <div class=\"system__sysdev__desc\" data-v-7dbd852c><div class=\"system__sysdev__desc__first\" data-v-7dbd852c>\n                これまでインフラ基盤に注力してきたこともあり、システム開発のケイパビリティはまだまだ微力です。ですが、ＢＦＴらしさを発揮したいという思いで、アプリケーションアーキテクトの仕事やAI関連の仕事に注力しています。\n            </div> <h3 class=\"system__sysdev__desc__about\" data-v-7dbd852c>\n                「FIWARE」を活用した<br class=\"sp\" data-v-7dbd852c>\n                スマートシティ向け<br class=\"sp\" data-v-7dbd852c>\n                「データ利活用基盤サービス」の開発\n            </h3> <div class=\"system__sysdev__desc__info\" data-v-7dbd852c><div class=\"system__sysdev__desc__info__img\" data-v-7dbd852c></div> <div class=\"system__sysdev__desc__info__txt\" data-v-7dbd852c>\n                    IoT等の情報を集約するクラウドサービスの開発で、VPS環境にDockerコンテナを構築し、Rancherでコンテナを管理する機能の設計と製造。開発言語はJavaScript(Node.js)で、FIWAREを使用した。\n                </div></div></div> <div class=\"red-bg\" data-v-7dbd852c><div class=\"red-bg__1\" data-v-7dbd852c></div> <div class=\"red-bg__2\" data-v-7dbd852c></div> <div class=\"red-bg__3\" data-v-7dbd852c></div> <div class=\"red-bg__4\" data-v-7dbd852c></div></div></section> <section class=\"system__infra\" data-v-7dbd852c><div class=\"side-red\" data-v-7dbd852c></div> <h2 class=\"system__infra__title\" data-v-7dbd852c><span class=\"ja\" data-v-7dbd852c>インフラ基盤構築</span></h2> <div class=\"system__infra__desc\" data-v-7dbd852c><div class=\"system__sysdev__desc__first\" data-v-7dbd852c>\n                商談で「インフラ基盤が強みです」というと「どこが得意？」と聞かれて困るときがあります。というのは「インフラ基盤全体をデザインする」を大切にしているからです。オンプレもクラウドもベンダもこだわらず「インフラ基盤全体を相談できる会社」でありつづけます。\n            </div> <h3 class=\"system__infra__desc__about\" data-v-7dbd852c>\n                ハイブリッドキャストサービスを提供するシステムの機能拡張\n            </h3> <div class=\"system__infra__desc__info\" data-v-7dbd852c><div class=\"system__infra__desc__info__img\" data-v-7dbd852c></div> <div class=\"system__infra__desc__info__txt\" data-v-7dbd852c>\n                    テレビをインターネットにつなぎ－IPTV、放送番組とウェブを連動させるサービスで、システムはパブリッククラウドのAmazon\n                    Web\n                    Services（AWS）に構築した。要件定義から構築・運用まで行い、AWSの運用費用を削減した。\n                </div></div></div></section>")],2)}
